
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import VueTree from '../vue-simple-tree/src/components/VueTree.vue'

export default {
directives: {
TransferDom
},
mounted () {



if (this.$route.params.treeData) {
this.treeData=this.$route.params.treeData
}
setTimeout(() => {
this.money = -1024
}, 2000)
},
components: {
Cell,
Group,
Checklist,
XButton,
Divider,
VueTree,
Card,
XDialog,
XInput,
Flexbox,
FlexboxItem
},
methods: {
onClick (e) {
window.event? window.event.cancelBubble = true : e.stopPropagation();

console.log('on click')
},
change (val, label) {
console.log('change', val, label)
},

tree_add(){
this.placeTitle="输入规格名称"
this.show=true;
this.operate_code=1
},
node_add(node){
this.show=true;
this.placeTitle="输入规格选项"
console.log(node)
this.operate_code=0
this.current_sel=this.treeData[node.the_id]
},
node_edit(){
console.log("编辑节点按钮")
}
,
node_delete(){
console.log("删除节点按钮")
},
complete(){
if (this.operate_code) {
this.treeData.push(
{
parent:-1,
id:this.treeData.length,
label:this.rootName,
children:[]

})
}else{
console.log(this.current_sel)
this.current_sel.children.push(
{
parent:this.current_sel.id,
id:this.current_sel.children.length,
label:this.rootName,
children:[]
}
)

}
this.Allclear()

},
submitAlter(){
console.log(this.$route.params.type)
if (this.$route.params.type) {
this.$http.post('http://localhost:8000/updateItem',
{
"goods_id":this.$route.params.goods_id,
"standards":JSON.stringify(this.treeData)

}).then(response => {
this.$router.replace({
path:'items',
query:{
goods_id:this.$route.params.goods_id,
}
})
}, response => {
console.log("error");
});
}else{
window.mycache.standards=this.treeData
console.log(this.treeData)
this.$router.back(-1)
}
}
,
Allclear(){
this.rootName=""
this.show=false;
}
},
data () {
return {
rootName:null,
show:false,
placeTitle:"",
operate_code:1,
del:"checkbox",//hidden
labelPosition: '',
commonList: [ 'China', 'Japan', 'America' ],
current_sel:{},
checklist001: [],
checkedIds: [],

treeData: [],

options: {

itemName: 'label',


addItem: true,


checkbox: false,


checkedIds: [],


checkedOpen: true,


folderBold: false,


openClass: 'fa fa-plus-square text-info',

closeClass: 'fa fa-minus-square text-danger',


addClass: 'fa fa-plus text-danger',


showEdit: true,


showDelete: true,


editClass: 'fa fa-edit',


deleteClass: 'fa fa-trash-o',


idsWithParent: true,


depthOpen: 0
},
list: [{
label: 'Apple',
value: '3.29'
}, {
label: 'Banana',
value: '1.04'
}, {
label: 'Fish',
value: '8.00'
}],
money: null,
showContent001: false,
showContent002: false,
showContent003: false,
showContent004: false
}
}
}



